export default {
  layout: "default",
  data() {
    return {
      data: [],
      next: ""
    };
  },
  async fetch() {
    var self = this;
    await self.$axios
      .$get(
        process.env.baseUrl + `/q&a/api/v1/channel/${self.$route.params.qaSlug}`
      )
      .then(function(posts) {
        self.data = posts.results;
        self.next = posts.next;
      })
      .catch(function(error) {
        console.log(error);
      })
      .finally(function() {});
  },
  methods: {
    async loadData() {
      if (this.next != null) {
        var self = this;
        await self.$axios
          .$get(self.next)
          .then(function(posts) {
            posts.results.List.forEach(element => {
              self.data.List.push(element);
            });
            self.next = posts.next;
          })
          .catch(function(error) {
            console.log("No Net" + error);
          })
          .finally(function() {});
      } else {
        // alert("Null");
      }
    },
    async setView(view, slug) {
      this.$axios
        .$put(process.env.baseUrl + `/q&a/api/v1/dtls/${slug}`, {
          view: view + 1
        })
        .then(function(response) {})
        .catch(function(e) {
          console.log(e);
        });
    }
  }
};
